    productType.name = '';
    productType.index = 1;
    productType.status = undefined;
    queryFindAll();
}

const TradeTypeDataclear = () => {
    tradeTypeData.status = '';
    tradeTypeData.label = '';
    tradeTypeData.id = 0;
}

const queryFindAll = async () => {
    table.loading = true;

    const { data: { rows, count } } = await findTradeTypeAll(productType.name, productType.index, productType.status)
    productTypeList.value = rows;
    total.value = count;
    table.loading = false;

    // const { name } = await findTradeTypeAll(productType.name, productType.index, productType.status);
