}

const dialogAddTrade = () => {
    TradeTypeDataclear();
    showDoaiog.isAdd = true;
    showDoaiog.title = "添加分类";
    showDoaiog.show = true;
}

const dialogChangeTrade = (item: tradeType) => {
    showDoaiog.isAdd = false;
    showDoaiog.title = "修改分类";
    showDoaiog.show = true;
    tradeTypeData.id = item.id;
    tradeTypeData.label = item.label;
    tradeTypeData.status = item.status;
}

const funUpdateTradeType = async (formEl: FormInstance | undefined) => {
    if (!formEl) return
    await formEl.validate(async (valid, fields) => {
        if (valid) {
            const { msg, code } = await updateTradeType(tradeTypeData.id, tradeTypeData.label, tradeTypeData.status)
            if (code == 200) {
                showDoaiog.show = false;
                queryFindAll();
                ElMessage.success(msg);
            }

        } else {
            console.log('error submit!', fields)
        }
    })




}

