    if (!formEl) return
    await formEl.validate(async (valid, fields) => {
        if (valid) {
            const { msg, code } = await addTradeType(tradeTypeData.label, tradeTypeData.status)
            if (code == 200) {
                showDoaiog.show = false;
                queryFindAll();
                ElMessage.success(msg);
            }
        } else {
            console.log('error submit!', fields)
        }
    })
}


