    const ids: number[] = multipleSelection.value.map(item => item.id);
    const { msg, code } = await tadeTypeDels(ids, 1)
    if (code == 200) {
        queryFindAll();
        ElMessage.success(msg);
    }
}

onMounted(() => {
    queryFindAll();
})



